import { tns } from 'tiny-slider/src/tiny-slider';

export default class Institutional {
  constructor() {
    this.sliderContainer = '.stripeArea__wrapper';

    this.componentBuilder();
  }

  menuActions() {
    const headerButtonMenu = document.querySelector('.header__button-menu');
    const headerWrapperMenu = document.querySelector('.header__wrapper--menu');

    const toogleMenu = () => {
      const icon = headerButtonMenu.children[0];

      icon.classList.contains('fa-bars')
        ? icon.classList.replace('fa-bars', 'fa-times')
        : icon.classList.replace('fa-times', 'fa-bars');

      document.body.classList.toggle('without-scroll');
      headerWrapperMenu.classList.toggle('header__wrapper--menu-show');
    };

    headerButtonMenu.addEventListener('click', toogleMenu);

    const navigationLinks = document.querySelectorAll(
      '.navigation__list > .navigation__link'
    );

    [...navigationLinks].forEach(link => {
      link.addEventListener('click', event => {
        toogleMenu();
      });
    });

    const subMenus = document.querySelectorAll('.navigation__list .list__item');

    [...subMenus].forEach(subMenu => {
      const subList = subMenu.querySelector('.navigation__sublist');
      const navigationLink = subMenu.querySelector('.navigation__link');

      navigationLink.addEventListener('click', ({ target }) => {
        subList && subList.classList.add('navigation__sublist--show');
      });

      const buttonBack = subList.querySelector('.header__button-back');

      buttonBack.addEventListener('click', event => {
        subList && subList.classList.remove('navigation__sublist--show');
      });
    });
  }

  headerSticky() {
    const header = document.querySelector('.header');
    const heroImageContainer = document.querySelector('.hero-image');

    document.body.addEventListener(
      'scroll',
      e => {
        const offSetValues = {
          windowY: document.body.scrollTop,
          documentY: document.documentElement.scrollTop,
        };

        if (offSetValues.windowY >= 20) {
          header.classList.add('header--sticky');
          heroImageContainer.style.paddingTop = `${header.offsetHeight}px`;
        } else {
          header.classList.remove('header--sticky');
          heroImageContainer.style.paddingTop = 0;
        }
      },
      true
    );
  }

  initSlider() {
    console.log($(this.sliderContainer));

    tns({
      container: `${this.sliderContainer}`,
      items: 1,
      nav: false,
      loop: false,
      speed: 500,
      controls: false,
      rewind: false,
      gutter: 16,
      touch: true,
      slideBy: 1,
      autoWidth: true,
      autoplay: false,
    });
  }

  componentBuilder() {
    document.addEventListener('DOMContentLoaded', event => {
      this.headerSticky();
      this.menuActions();
      this.initSlider();
    });
  }
}

new Institutional();
